import http from '../http';

/**
 * Collector API Service
 */
export const collectorAPI = {
  /**
   * Get all collectors for a survey
   * @param {number} surveyId - Survey ID
   * @returns {Promise} - Response with collectors array
   */
  getCollectors: async (surveyId) => {
    return http.get(`/surveys/${surveyId}/collectors`);
  },

  /**
   * Get single collector by ID
   * @param {number} surveyId - Survey ID
   * @param {number} collectorId - Collector ID
   * @returns {Promise} - Response with collector data
   */
  getCollector: async (surveyId, collectorId) => {
    return http.get(`/surveys/${surveyId}/collectors/${collectorId}`);
  },

  /**
   * Create new collector
   * @param {number} surveyId - Survey ID
   * @param {Object} collectorData - Collector data (type, name, config)
   * @returns {Promise} - Response with created collector
   */
  createCollector: async (surveyId, collectorData) => {
    return http.post(`/surveys/${surveyId}/collectors`, collectorData);
  },

  /**
   * Update collector
   * @param {number} surveyId - Survey ID
   * @param {number} collectorId - Collector ID
   * @param {Object} collectorData - Updated collector data
   * @returns {Promise} - Response with updated collector
   */
  updateCollector: async (surveyId, collectorId, collectorData) => {
    return http.patch(`/surveys/${surveyId}/collectors/${collectorId}`, collectorData);
  },

  /**
   * Delete collector
   * @param {number} surveyId - Survey ID
   * @param {number} collectorId - Collector ID
   * @returns {Promise} - Response
   */
  deleteCollector: async (surveyId, collectorId) => {
    return http.delete(`/surveys/${surveyId}/collectors/${collectorId}`);
  },

  /**
   * Toggle collector active status
   * @param {number} surveyId - Survey ID
   * @param {number} collectorId - Collector ID
   * @param {boolean} isActive - New active status
   * @returns {Promise} - Response with updated collector
   */
  toggleCollector: async (surveyId, collectorId, isActive) => {
    return http.patch(`/surveys/${surveyId}/collectors/${collectorId}`, { is_active: isActive });
  },
};
