import React, { useEffect, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { surveyAPI } from '../../../api/services/survey.service';
import Button from '../../../components/UI/Button';
import Card from '../../../components/UI/Card';
import styles from './Detail.module.scss';

const SurveysDetail = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const [survey, setSurvey] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');

  useEffect(() => {
    fetchSurvey();
  }, [id]);

  const fetchSurvey = async () => {
    try {
      setLoading(true);
      const response = await surveyAPI.getSurvey(id);
      if (response.success) {
        setSurvey(response.data);
      } else {
        setError(response.message || 'Failed to load survey');
      }
    } catch (err) {
      setError(err.message || 'An error occurred');
    } finally {
      setLoading(false);
    }
  };

  const handleStatusChange = async (action) => {
    try {
      const response = await surveyAPI.changeSurveyStatus(id, action);
      if (response.success) {
        fetchSurvey();
      }
    } catch (err) {
      console.error('Error changing status:', err);
    }
  };

  if (loading) {
    return (
      <div className={styles.loading}>
        <div className={styles.spinner}></div>
        <p>Loading survey...</p>
      </div>
    );
  }

  if (error || !survey) {
    return (
      <div className={styles.error}>
        <p>{error || 'Survey not found'}</p>
        <Button onClick={() => navigate('/surveys')}>Back to Surveys</Button>
      </div>
    );
  }

  return (
    <div className={styles.detailContainer}>
      <div className={styles.header}>
        <div>
          <h1>{survey.title}</h1>
          <p className={styles.description}>{survey.description}</p>
        </div>
        <div className={styles.actions}>
          {survey.status === 'draft' && (
            <Button variant="success" onClick={() => handleStatusChange('activate')}>
              Activate
            </Button>
          )}
          {survey.status === 'active' && (
            <Button variant="secondary" onClick={() => handleStatusChange('pause')}>
              Pause
            </Button>
          )}
          <Button variant="secondary" onClick={() => navigate(`/surveys/${id}/collectors`)}>
            Collectors
          </Button>
          <Button variant="primary" onClick={() => navigate(`/surveys/${id}/analytics`)}>
            Analytics
          </Button>
        </div>
      </div>

      <Card title="Survey Information">
        <div className={styles.infoGrid}>
          <div className={styles.infoItem}>
            <span className={styles.label}>Status:</span>
            <span className={`${styles.badge} ${styles[survey.status]}`}>
              {survey.status}
            </span>
          </div>
          <div className={styles.infoItem}>
            <span className={styles.label}>Responses:</span>
            <span className={styles.value}>{survey.response_count || 0}</span>
          </div>
          <div className={styles.infoItem}>
            <span className={styles.label}>Created:</span>
            <span className={styles.value}>{new Date(survey.created_at).toLocaleDateString()}</span>
          </div>
        </div>
      </Card>
    </div>
  );
};

export default SurveysDetail;
