import React from 'react';
import { BrowserRouter, Routes, Route, Navigate } from 'react-router-dom';

// Layouts
import DefaultLayout from '../components/Layout/DefaultLayout';
import HeaderOnly from '../components/Layout/HeaderOnly';

// Pages
import Login from '../pages/Auth/Login';
import SurveysList from '../pages/Surveys/List';
import SurveysCreate from '../pages/Surveys/Create';
import SurveysDetail from '../pages/Surveys/Detail';
import CollectorsManage from '../pages/Collectors/Manage';
import AnalyticsDashboard from '../pages/Analytics/Dashboard';

// ===========================
// PROTECTED ROUTE COMPONENT
// ===========================
/**
 * ProtectedRoute - Wraps routes that require authentication
 * @param {ReactNode} children - Child components to render
 * @param {Array<string>} allowedRoles - Array of roles that can access this route
 * @param {string} layout - Layout component to use ('default' or 'headerOnly')
 */
const ProtectedRoute = ({ children, allowedRoles = [], layout = 'default' }) => {
  const token = localStorage.getItem('access_token');
  const userStr = localStorage.getItem('user');
  
  // Check if user is authenticated
  if (!token || !userStr) {
    return <Navigate to="/auth/login" replace />;
  }

  // Parse user data
  let user;
  try {
    user = JSON.parse(userStr);
  } catch (error) {
    console.error('Failed to parse user data:', error);
    localStorage.removeItem('access_token');
    localStorage.removeItem('refresh_token');
    localStorage.removeItem('user');
    return <Navigate to="/auth/login" replace />;
  }

  // Check if user has required role
  if (allowedRoles.length > 0 && !allowedRoles.includes(user.role)) {
    return (
      <div style={{ padding: '20px', textAlign: 'center' }}>
        <h2>403 - Forbidden</h2>
        <p>You don't have permission to access this page.</p>
        <p>Required role: {allowedRoles.join(' or ')}</p>
        <p>Your role: {user.role}</p>
        <button onClick={() => window.history.back()}>Go Back</button>
      </div>
    );
  }

  // Render with appropriate layout
  const LayoutComponent = layout === 'headerOnly' ? HeaderOnly : DefaultLayout;
  return <LayoutComponent>{children}</LayoutComponent>;
};

// ===========================
// WITH ROLE HOC
// ===========================
/**
 * withRole - Higher Order Component for role-based access control
 * Wraps a component and checks if user has required role before rendering
 * 
 * @param {React.Component} Component - Component to wrap
 * @param {Array<string>} allowedRoles - Array of roles that can access this component
 * @returns {React.Component} - Wrapped component with role checking
 * 
 * @example
 * export default withRole(MyComponent, ['admin', 'creator']);
 */
export const withRole = (Component, allowedRoles = []) => {
  return (props) => {
    const userStr = localStorage.getItem('user');
    
    if (!userStr) {
      return <Navigate to="/auth/login" replace />;
    }

    let user;
    try {
      user = JSON.parse(userStr);
    } catch (error) {
      console.error('Failed to parse user data:', error);
      return <Navigate to="/auth/login" replace />;
    }

    // Check role access
    if (allowedRoles.length > 0 && !allowedRoles.includes(user.role)) {
      return (
        <div style={{ padding: '20px', textAlign: 'center' }}>
          <h2>403 - Access Denied</h2>
          <p>You don't have the required permissions.</p>
          <p>Required: {allowedRoles.join(', ')}</p>
          <p>Your role: {user.role}</p>
        </div>
      );
    }

    return <Component {...props} />;
  };
};

// ===========================
// PUBLIC ROUTE GUARD
// ===========================
/**
 * PublicRoute - Wraps public routes (like login)
 * Redirects to home if user is already authenticated
 */
const PublicRoute = ({ children }) => {
  const token = localStorage.getItem('access_token');
  
  if (token) {
    return <Navigate to="/surveys" replace />;
  }

  return children;
};

// ===========================
// MAIN ROUTER
// ===========================
const AppRouter = () => {
  return (
    <BrowserRouter>
      <Routes>
        {/* Root - Redirect to surveys if authenticated, login otherwise */}
        <Route 
          path="/" 
          element={
            localStorage.getItem('access_token') 
              ? <Navigate to="/surveys" replace /> 
              : <Navigate to="/auth/login" replace />
          } 
        />

        {/* Public Routes */}
        <Route 
          path="/auth/login" 
          element={
            <PublicRoute>
              <Login />
            </PublicRoute>
          } 
        />

        {/* Protected Routes - Surveys */}
        <Route
          path="/surveys"
          element={
            <ProtectedRoute 
              allowedRoles={['owner', 'admin', 'creator', 'viewer']}
              layout="default"
            >
              <SurveysList />
            </ProtectedRoute>
          }
        />

        <Route
          path="/surveys/create"
          element={
            <ProtectedRoute 
              allowedRoles={['owner', 'admin', 'creator']}
              layout="default"
            >
              <SurveysCreate />
            </ProtectedRoute>
          }
        />

        <Route
          path="/surveys/:id"
          element={
            <ProtectedRoute 
              allowedRoles={['owner', 'admin', 'creator', 'viewer']}
              layout="default"
            >
              <SurveysDetail />
            </ProtectedRoute>
          }
        />

        {/* Protected Routes - Collectors */}
        <Route
          path="/surveys/:id/collectors"
          element={
            <ProtectedRoute 
              allowedRoles={['owner', 'admin', 'creator', 'collector_manager']}
              layout="default"
            >
              <CollectorsManage />
            </ProtectedRoute>
          }
        />

        {/* Protected Routes - Analytics */}
        <Route
          path="/surveys/:id/analytics"
          element={
            <ProtectedRoute 
              allowedRoles={['owner', 'admin', 'creator', 'viewer']}
              layout="default"
            >
              <AnalyticsDashboard />
            </ProtectedRoute>
          }
        />

        {/* 404 Not Found */}
        <Route 
          path="*" 
          element={
            <div style={{ padding: '20px', textAlign: 'center' }}>
              <h2>404 - Page Not Found</h2>
              <p>The page you're looking for doesn't exist.</p>
              <button onClick={() => window.location.href = '/surveys'}>
                Go to Surveys
              </button>
            </div>
          } 
        />
      </Routes>
    </BrowserRouter>
  );
};

export default AppRouter;

// ===========================
// ROLE DEFINITIONS (Reference)
// ===========================
/**
 * Available Roles:
 * - owner: Full system access
 * - admin: Administrative access to all surveys
 * - creator: Can create and manage own surveys
 * - viewer: Read-only access to surveys
 * - collector_manager: Manage distribution channels
 * - respondent: Can only submit responses
 * - analyst: Read-only access for analysis
 */
