import React, { useEffect, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { collectorAPI } from '../../../api/services/collector.service';
import Button from '../../../components/UI/Button';
import Card from '../../../components/UI/Card';
import Table from '../../../components/UI/Table';
import Input from '../../../components/UI/Input';
import Select from '../../../components/UI/Select';
import styles from './Manage.module.scss';

const CollectorsManage = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const [collectors, setCollectors] = useState([]);
  const [loading, setLoading] = useState(true);
  const [showCreate, setShowCreate] = useState(false);
  const [formData, setFormData] = useState({
    type: 'web',
    name: '',
    config: {}
  });

  useEffect(() => {
    fetchCollectors();
  }, [id]);

  const fetchCollectors = async () => {
    try {
      setLoading(true);
      const response = await collectorAPI.getCollectors(id);
      if (response.success) {
        setCollectors(response.data || []);
      }
    } catch (err) {
      console.error('Error fetching collectors:', err);
    } finally {
      setLoading(false);
    }
  };

  const handleCreate = async (e) => {
    e.preventDefault();
    try {
      const response = await collectorAPI.createCollector(id, formData);
      if (response.success) {
        fetchCollectors();
        setShowCreate(false);
        setFormData({ type: 'web', name: '', config: {} });
      }
    } catch (err) {
      console.error('Error creating collector:', err);
    }
  };

  const handleToggle = async (collectorId, isActive) => {
    try {
      await collectorAPI.toggleCollector(id, collectorId, !isActive);
      fetchCollectors();
    } catch (err) {
      console.error('Error toggling collector:', err);
    }
  };

  const columns = [
    { key: 'id', label: 'ID', width: '80px' },
    { key: 'name', label: 'Name' },
    { key: 'type', label: 'Type', width: '120px' },
    {
      key: 'is_active',
      label: 'Status',
      width: '120px',
      render: (row) => (
        <span className={`${styles.badge} ${row.is_active ? styles.active : styles.inactive}`}>
          {row.is_active ? 'Active' : 'Inactive'}
        </span>
      )
    },
    {
      key: 'actions',
      label: 'Actions',
      width: '150px',
      render: (row) => (
        <div className={styles.actions}>
          <Button
            size="small"
            variant={row.is_active ? 'secondary' : 'success'}
            onClick={() => handleToggle(row.id, row.is_active)}
          >
            {row.is_active ? 'Disable' : 'Enable'}
          </Button>
        </div>
      )
    }
  ];

  return (
    <div className={styles.collectorsContainer}>
      <div className={styles.header}>
        <div>
          <h1>Collectors</h1>
          <p>Manage distribution channels for Survey #{id}</p>
        </div>
        <div className={styles.headerActions}>
          <Button variant="secondary" onClick={() => navigate(`/surveys/${id}`)}>
            Back to Survey
          </Button>
          <Button variant="primary" onClick={() => setShowCreate(!showCreate)}>
            {showCreate ? 'Cancel' : '+ Add Collector'}
          </Button>
        </div>
      </div>

      {showCreate && (
        <Card title="Create New Collector">
          <form onSubmit={handleCreate} className={styles.form}>
            <Input
              label="Collector Name"
              value={formData.name}
              onChange={(e) => setFormData({ ...formData, name: e.target.value })}
              required
            />
            <Select
              label="Type"
              value={formData.type}
              onChange={(e) => setFormData({ ...formData, type: e.target.value })}
              options={[
                { value: 'web', label: 'Web Link' },
                { value: 'email', label: 'Email' },
                { value: 'embed', label: 'Embedded' }
              ]}
            />
            <Button type="submit" variant="primary">Create Collector</Button>
          </form>
        </Card>
      )}

      <Card>
        {loading ? (
          <div className={styles.loading}>
            <div className={styles.spinner}></div>
            <p>Loading collectors...</p>
          </div>
        ) : collectors.length === 0 ? (
          <div className={styles.empty}>
            <p>No collectors yet</p>
            <Button onClick={() => setShowCreate(true)}>Create First Collector</Button>
          </div>
        ) : (
          <Table columns={columns} data={collectors} />
        )}
      </Card>
    </div>
  );
};

export default CollectorsManage;
