import React from 'react';
import styles from './Table.module.scss';

/**
 * Table Component
 * @param {Array} columns - Column definitions [{key, label, width, render}]
 * @param {Array} data - Table data array
 * @param {boolean} striped - Striped rows
 * @param {boolean} hoverable - Hover effect on rows
 * @param {function} onRowClick - Row click handler
 */
const Table = ({
  columns = [],
  data = [],
  striped = true,
  hoverable = true,
  onRowClick = null,
  ...props
}) => {
  return (
    <div className={styles.tableWrapper}>
      <table className={styles.table} {...props}>
        <thead className={styles.thead}>
          <tr>
            {columns.map((column, index) => (
              <th
                key={column.key || index}
                className={styles.th}
                style={{ width: column.width || 'auto' }}
              >
                {column.label}
              </th>
            ))}
          </tr>
        </thead>
        <tbody className={styles.tbody}>
          {data.length === 0 ? (
            <tr>
              <td colSpan={columns.length} className={styles.emptyState}>
                No data available
              </td>
            </tr>
          ) : (
            data.map((row, rowIndex) => (
              <tr
                key={row.id || rowIndex}
                className={`
                  ${styles.tr}
                  ${striped ? styles.striped : ''}
                  ${hoverable ? styles.hoverable : ''}
                  ${onRowClick ? styles.clickable : ''}
                `}
                onClick={() => onRowClick && onRowClick(row)}
              >
                {columns.map((column, colIndex) => (
                  <td key={column.key || colIndex} className={styles.td}>
                    {column.render ? column.render(row, rowIndex) : row[column.key]}
                  </td>
                ))}
              </tr>
            ))
          )}
        </tbody>
      </table>
    </div>
  );
};

export default Table;
