import http from '../http';

/**
 * Survey API Service
 */
export const surveyAPI = {
  /**
   * Get all surveys
   * @param {Object} params - Query parameters (status, page, limit)
   * @returns {Promise} - Response with surveys array
   */
  getSurveys: async (params = {}) => {
    const queryString = new URLSearchParams(params).toString();
    const endpoint = queryString ? `/surveys?${queryString}` : '/surveys';
    return http.get(endpoint);
  },

  /**
   * Get single survey by ID
   * @param {number} surveyId - Survey ID
   * @returns {Promise} - Response with survey data
   */
  getSurvey: async (surveyId) => {
    return http.get(`/surveys/${surveyId}`);
  },

  /**
   * Create new survey
   * @param {Object} surveyData - Survey data (title, description, template_id, etc.)
   * @returns {Promise} - Response with created survey
   */
  createSurvey: async (surveyData) => {
    return http.post('/surveys', surveyData);
  },

  /**
   * Update survey
   * @param {number} surveyId - Survey ID
   * @param {Object} surveyData - Updated survey data
   * @returns {Promise} - Response with updated survey
   */
  updateSurvey: async (surveyId, surveyData) => {
    return http.patch(`/surveys/${surveyId}`, surveyData);
  },

  /**
   * Delete survey
   * @param {number} surveyId - Survey ID
   * @returns {Promise} - Response
   */
  deleteSurvey: async (surveyId) => {
    return http.delete(`/surveys/${surveyId}`);
  },

  /**
   * Change survey status (activate, pause, close)
   * @param {number} surveyId - Survey ID
   * @param {string} action - Action (activate, pause, close)
   * @returns {Promise} - Response with updated survey
   */
  changeSurveyStatus: async (surveyId, action) => {
    return http.patch(`/surveys/${surveyId}/status`, { action });
  },
};
