import http from '../http';

/**
 * Authentication API Service
 */
export const authAPI = {
  /**
   * User login
   * @param {string} username - Username
   * @param {string} password - Password
   * @returns {Promise} - Response with tokens and user data
   */
  login: async (username, password) => {
    return http.post('/auth/login', { username, password });
  },

  /**
   * User logout
   * @returns {Promise} - Response
   */
  logout: async () => {
    const refreshToken = localStorage.getItem('refresh_token');
    return http.post('/auth/logout', { refresh_token: refreshToken });
  },

  /**
   * Refresh access token
   * @returns {Promise} - Response with new tokens
   */
  refresh: async () => {
    const refreshToken = localStorage.getItem('refresh_token');
    return http.post('/auth/refresh', { refresh_token: refreshToken });
  },

  /**
   * Get current user profile
   * @returns {Promise} - Response with user data
   */
  getProfile: async () => {
    return http.get('/auth/profile');
  },
};
