import React, { useEffect, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { analyticsAPI } from '../../../api/services/analytics.service';
import Button from '../../../components/UI/Button';
import Card from '../../../components/UI/Card';
import styles from './Dashboard.module.scss';

const AnalyticsDashboard = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const [summary, setSummary] = useState(null);
  const [distributions, setDistributions] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchAnalytics();
  }, [id]);

  const fetchAnalytics = async () => {
    try {
      setLoading(true);
      const [summaryRes, distRes] = await Promise.all([
        analyticsAPI.getSummary(id),
        analyticsAPI.getAllDistributions(id)
      ]);

      if (summaryRes.success) setSummary(summaryRes.data);
      if (distRes.success) setDistributions(distRes.data || []);
    } catch (err) {
      console.error('Error fetching analytics:', err);
    } finally {
      setLoading(false);
    }
  };

  const handleExport = () => {
    const url = analyticsAPI.exportCSV(id);
    window.open(url, '_blank');
  };

  if (loading) {
    return (
      <div className={styles.loading}>
        <div className={styles.spinner}></div>
        <p>Loading analytics...</p>
      </div>
    );
  }

  return (
    <div className={styles.analyticsContainer}>
      <div className={styles.header}>
        <div>
          <h1>Analytics Dashboard</h1>
          <p>Survey #{id} Performance Metrics</p>
        </div>
        <div className={styles.headerActions}>
          <Button variant="secondary" onClick={() => navigate(`/surveys/${id}`)}>
            Back to Survey
          </Button>
          <Button variant="primary" onClick={handleExport}>
             Export CSV
          </Button>
        </div>
      </div>

      {/* Summary Cards */}
      {summary && (
        <div className={styles.summaryGrid}>
          <Card title="Total Responses">
            <div className={styles.statValue}>{summary.total_responses || 0}</div>
          </Card>
          <Card title="Completed">
            <div className={styles.statValue}>{summary.completed_responses || 0}</div>
          </Card>
          <Card title="Avg. Time">
            <div className={styles.statValue}>
              {summary.average_completion_time 
                ? `${Math.round(summary.average_completion_time / 60)}m` 
                : 'N/A'}
            </div>
          </Card>
          <Card title="Completion Rate">
            <div className={styles.statValue}>
              {summary.completion_rate 
                ? `${Math.round(summary.completion_rate * 100)}%` 
                : 'N/A'}
            </div>
          </Card>
        </div>
      )}

      {/* Question Distributions */}
      <Card title="Response Distributions">
        {distributions.length === 0 ? (
          <p className={styles.noData}>No distribution data available</p>
        ) : (
          <div className={styles.distributionsList}>
            {distributions.map((dist) => (
              <div key={dist.question_id} className={styles.distributionItem}>
                <h3 className={styles.questionText}>{dist.question_text}</h3>
                <div className={styles.optionsList}>
                  {dist.options && dist.options.map((option) => (
                    <div key={option.option_id} className={styles.optionBar}>
                      <div className={styles.optionLabel}>
                        <span>{option.option_text}</span>
                        <span className={styles.optionCount}>{option.response_count}</span>
                      </div>
                      <div className={styles.barWrapper}>
                        <div
                          className={styles.barFill}
                          style={{ width: `${option.percentage}%` }}
                        ></div>
                      </div>
                      <span className={styles.optionPercentage}>
                        {Math.round(option.percentage)}%
                      </span>
                    </div>
                  ))}
                </div>
              </div>
            ))}
          </div>
        )}
      </Card>
    </div>
  );
};

export default AnalyticsDashboard;
