import http from '../http';

/**
 * Analytics API Service
 */
export const analyticsAPI = {
  /**
   * Get survey summary statistics
   * @param {number} surveyId - Survey ID
   * @returns {Promise} - Response with summary stats (total, completed, avg_time, etc.)
   */
  getSummary: async (surveyId) => {
    return http.get(`/surveys/${surveyId}/analytics/summary`);
  },

  /**
   * Get question distribution (response counts per option)
   * @param {number} surveyId - Survey ID
   * @param {number} questionId - Question ID
   * @returns {Promise} - Response with distribution data
   */
  getDistribution: async (surveyId, questionId) => {
    return http.get(`/surveys/${surveyId}/analytics/distribution/${questionId}`);
  },

  /**
   * Get all distributions for survey
   * @param {number} surveyId - Survey ID
   * @returns {Promise} - Response with all question distributions
   */
  getAllDistributions: async (surveyId) => {
    return http.get(`/surveys/${surveyId}/analytics/distribution`);
  },

  /**
   * Export survey responses as CSV
   * @param {number} surveyId - Survey ID
   * @returns {Promise} - CSV file download
   */
  exportCSV: async (surveyId) => {
    // Note: This returns the full URL for download
    const token = localStorage.getItem('access_token');
    const baseURL = process.env.REACT_APP_API_URL || 'http://localhost:5000/api/v1';
    return `${baseURL}/surveys/${surveyId}/export.csv?token=${token}`;
  },
};
