import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { surveyAPI } from '../../../api/services/survey.service';
import Button from '../../../components/UI/Button';
import Card from '../../../components/UI/Card';
import Table from '../../../components/UI/Table';
import styles from './List.module.scss';

const SurveysList = () => {
  const navigate = useNavigate();
  const [surveys, setSurveys] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const user = JSON.parse(localStorage.getItem('user') || '{}');

  useEffect(() => {
    fetchSurveys();
  }, []);

  const fetchSurveys = async () => {
    try {
      setLoading(true);
      setError('');
      const response = await surveyAPI.getSurveys();
      
      if (response.success) {
        setSurveys(response.data || []);
      } else {
        setError(response.message || 'Failed to load surveys');
      }
    } catch (err) {
      console.error('Error fetching surveys:', err);
      setError(err.message || 'An error occurred while loading surveys');
    } finally {
      setLoading(false);
    }
  };

  const columns = [
    {
      key: 'id',
      label: 'ID',
      width: '80px'
    },
    {
      key: 'title',
      label: 'Survey Title',
      render: (row) => (
        <div>
          <div className={styles.surveyTitle}>{row.title}</div>
          {row.description && (
            <div className={styles.surveyDescription}>{row.description}</div>
          )}
        </div>
      )
    },
    {
      key: 'status',
      label: 'Status',
      width: '120px',
      render: (row) => (
        <span className={`${styles.badge} ${styles[`badge${row.status}`]}`}>
          {row.status}
        </span>
      )
    },
    {
      key: 'response_count',
      label: 'Responses',
      width: '120px',
      render: (row) => (
        <div className={styles.responseCount}>
          {row.response_count || 0}
        </div>
      )
    },
    {
      key: 'created_at',
      label: 'Created',
      width: '150px',
      render: (row) => new Date(row.created_at).toLocaleDateString()
    },
    {
      key: 'actions',
      label: 'Actions',
      width: '200px',
      render: (row) => (
        <div className={styles.actions}>
          <Button
            variant="secondary"
            size="small"
            onClick={() => navigate(`/surveys/${row.id}`)}
          >
            View
          </Button>
          <Button
            variant="secondary"
            size="small"
            onClick={() => navigate(`/surveys/${row.id}/analytics`)}
          >
            Analytics
          </Button>
        </div>
      )
    }
  ];

  const canCreate = ['owner', 'admin', 'creator'].includes(user.role);

  return (
    <div className={styles.surveysListContainer}>
      <div className={styles.header}>
        <div>
          <h1>Surveys</h1>
          <p className={styles.subtitle}>
            Manage and view all your surveys
          </p>
        </div>
        {canCreate && (
          <Button
            variant="primary"
            onClick={() => navigate('/surveys/create')}
          >
            + Create Survey
          </Button>
        )}
      </div>

      {error && (
        <div className={styles.errorMessage}>
          <span className={styles.errorIcon}></span>
          {error}
          <Button
            variant="secondary"
            size="small"
            onClick={fetchSurveys}
          >
            Retry
          </Button>
        </div>
      )}

      <Card>
        {loading ? (
          <div className={styles.loading}>
            <div className={styles.spinner}></div>
            <p>Loading surveys...</p>
          </div>
        ) : surveys.length === 0 ? (
          <div className={styles.emptyState}>
            <div className={styles.emptyIcon}></div>
            <h3>No surveys yet</h3>
            <p>Get started by creating your first survey</p>
            {canCreate && (
              <Button
                variant="primary"
                onClick={() => navigate('/surveys/create')}
              >
                Create Survey
              </Button>
            )}
          </div>
        ) : (
          <Table
            columns={columns}
            data={surveys}
          />
        )}
      </Card>
    </div>
  );
};

export default SurveysList;
